/* VARIABLES */
#root {
  --color-red: #EE0909;
  --color-white: #ffffff;
  --color-yellow: #E87E00;
  --color-bright-yellow: #d4a821;
  --color-brik-red: #D83F31;
  --color-dark-purple: #28181B;
  --color-light-purple: #BA9A93;
  --color-green: #228b6e;
  --color-purple: #a52a96;
  --bgcolor: var(--color-green);
  --color-light-yellow: #D19E60;
  --color-black: #111211;
  --color-shadow: rgba(0, 255, 157, 0.7);
  --color-gray: #D9D9D9;
  --nav-font: 'Alegreya';
  --nav-button: 'Galindo';
  --title-font: 'Kanit';
  --button-font: 'Roboto Condensed';
  --name-font: 'Happy Monkey';
  --normal-text-font: 'Cabin';


  background-color: var(--color-dark-purple);
  color: black;
}

/* ----------------------------------------------------------------------------------------------------- */

/* APP */

.App{
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  width: 100%;
}

#page-body{
  border-radius: 0 0 2rem 2rem;
  background-color: var(--bgcolor);
  margin-top: auto;
}
/* ----------------------------------------------------------------------------------------------------- */

/* NAVIGATION BAR */

.nav_bar {
  overflow: hidden;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0;
  gap: .5rem;
  background-color: var(--color-dark-purple);
  border-radius: 0 0 2rem 2rem;
}

.nav_link {
  color: white;
  font-family: var(--nav-font);
  font-size: 1.2rem;
  font-weight: 400;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  align-self:center;
  text-decoration: none;
  border: .1rem solid white;
  background-color: var(--color-brik-red);
  width: 18%;
  padding:.5rem .5rem;
  transition:all .5s ease;
  letter-spacing:1px;
  outline:none;

}

#left_outer_link{
  border-radius: .5rem .5rem .5rem 5rem;
}

#right_outer_link{
  border-radius: .5rem .5rem 2rem .5rem;
}

.inner_nav{
  border-radius: .5rem;
}

.nav_link:hover {
  background-color: var(--color-yellow);
  color: black;
  font-size: 1.5rem;
}

.nav_link:active{
  background-color: var(--color-yellow);
  color: black;
}

@media(min-width: 800px){
  .nav_link{
    width: 23%;
  }
}
/* ----------------------------------------------------------------------------------------------------- */
/* ALL PAGE */

.margined-page{
  margin: 2rem 2rem;
  height: 100%;
}

.wrapped-page{
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
} 


.left-border-decor{
  background-color: var(--color-dark-purple);
  margin-right: 2rem;
}

.submit-button{
  font-family: var(--button-font);
  font-size: 1.8rem;
  font-weight: 700;
  color: var(--color-yellow);
  letter-spacing: .02rem;
  align-self: center; 
  background-color: var(--color-green);
  cursor: pointer;
  border: .2rem solid currentColor;
  border-radius: .8rem;
  padding: .4rem 1rem;
  position: relative;
  transition: color .3s;
  z-index: 1;

}

.submit-button::before, .submit-button::after {
    content: '';
    position: absolute;
    background-color: var(--color-green);
    z-index: -1;
    transition: all .3s;

  }
.submit-button::before {
  width: calc(100% - 3rem);
  height: calc(101% + 1rem);
  top: -.5rem;
  left: 50%;
  transform: translateX(-50%);
}
.submit-button::after {
  height: calc(100% - 2rem);
  width: calc(101% + 1rem);
  left: -.5rem;
  top: 50%;
  transform: translateY(-50%);
}
.submit-button:hover {
  color: white;
}
.submit-button:hover::before { width: 0; }
.submit-button:hover::after { height: 0; }
.submit-button:active { transform: translateY(.2rem); }

.normal_text{
  font-size: 18px;
  text-align: justify;
  letter-spacing: 0.7px;
}

/* ----------------------------------------------------------------------------------------------------- */

/* HOME PAGE */

/* greetings */
.aboutMe-greeting{
  flex: 40%;
  display: flex;
  flex-direction: column;
  font-family: var(--button-font);
  font-weight: 400;
  font-size: 1.5rem;
}

.aboutMe-greeting-container{
  grid-area: greeting;
  display: flex;
  align-items: center;
  width: 100%;
}

/* image */
.home-photo-container{
  grid-area: image;
  height: 15rem;
  justify-self: center;
  padding: 2rem 0;
}


/* description */

.aboutMe-author{
  grid-area: title;
  font-family: var(--nav-font);
  font-weight: 400;
  font-size: 2.5rem;
  letter-spacing: 6px;
  text-align: center;
}

#aboutMe-desc{
  grid-area: about;
  text-align: justify;
}

.aboutMe-contact-container{
  padding-top: 2rem;
  display: flex;
  width: 100%;
  justify-content: space-between;
}
.aboutMe-tag-container{
  grid-area: icons;
  display: flex;
  gap: 2rem;
}

#aboutMe-resume{
  font-size: 1.2rem !important;
}

.aboutMe-contact-tags{
  font-size: .5rem !important;
  color: var(--color-yellow);
}

.aboutMe-profile_desc{
  grid-area: desc;
  display: grid;
  grid-template-areas: 
  "title" 
  "icons"
  "about"
  ;
  font-family: var(--normal-text-font);
  grid-row-gap: 2rem;
  width: 90%;
  justify-self: center;
  align-items: center;
}


/* closing image */
.closing-photo{
  height: 10rem;
  margin:0;
  /* rotate: -25deg; */
}
.closing-image-container{
  grid-area: flower;
  display: flex;
  justify-content: center;
  align-items: center;
}


/* home page display */
.home-page{
  display: grid;
  grid-template-areas: 
  "greeting" 
  "image"
  "desc"
  "flower"
  ;
}


@media(min-width: 800px){
  .aboutMe-author{
    font-weight: 600;
    font-size: 3.5rem;
    text-align: left;
    padding: 2rem 0;
  }
  .aboutMe-profile_desc{
    display: flex;
    flex-direction: column;
    gap: 1.2rem;
    align-items: flex-start;
    width: 100%;
  }
  .aboutMe-contact-container{
    gap: 6rem;
    justify-content: center;
  }
  #aboutMe-desc{
    text-align: left;
    width: 90%;
  }
  .aboutMe-contact-tags{
    font-size: .8rem !important;
  }
  #aboutMe-resume{
    font-size: 2rem !important;
  }
  .home-photo-container{
    height: 25rem;
  }
  .aboutMe-greeting{
    font-size: 2rem!important;
  }
  .home-page{
    grid-template-areas: 
    "greeting greeting" 
    "desc image"
    "flower image"
    ;
  }

}
/* ----------------------------------------------------------------------------------------------------- */

/* SKILLS PAGE */
.work-left-container{
  display: flex;
  flex-direction: column;
  height: 100%;
  width: 100%;
}
.work-photo-container{
  display: flex;
  justify-content: center;
  align-items: center;
}
.work-photo{
  max-height: 40%;
}

.work-citation{
  flex:0.7;
  display: flex;
  flex-direction: column;
  font-family: var(--normal-text-font);
  font-size: 1.2rem;
  gap: 1.5rem;
}
.work-testimony-message{
  text-align: justify;
}
.work-quote-decor{
  display: flex;
  gap: 0.8rem;

}
.work-testimony, .work-testimony-author{
  width: 90%;
  padding: 1rem;
}
.quote-icons{
  font-size: 1.5rem;
}
#quote-left{
  padding-right: 1rem;
}
#quote-right{
  padding-left: 1rem;
}


.work-testimony-author{
  text-align: right;
}
.work-right-container{
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center !important;
  align-items: center !important;
  gap: 2rem;
  --barItem-font-size: .6rem;
}

.work-skills_icons{
  width: 95%;
  display: flex;
  justify-content: space-between;
  padding-top: 3rem;
}

.work-button, .work-button-active{
  border-radius: 5rem;
  padding: 0.8rem 0;
  font-family: var(--button-font);
  font-size: .8rem;
  text-align: center !important;
  background-color: #157e60;
  width: 6rem;

}
.work-button{
  border: 2px outset #0c4736;
  color: #b1fce7;

}

.work-button:hover, .work-button-active{
  border: 1px inset rgb(7, 26, 17);
  box-shadow: 5px 10px 20px rgb(4, 20, 11) inset;
  background-color: #09382a;
  color: var(--bgcolor);
  -webkit-text-stroke: 1px black;

}
.work-button:active { 
  box-shadow: 5px 20px 40px rgb(4, 20, 11) inset;
 }


 @media(min-width: 800px){
  .work-right-container{
    --barItem-font-size: 1.1rem;
  }
  .work-skills_icons{
    justify-content: center;
    gap: 3rem;
  }
  .work-button, .work-button-active{
    width: 9rem;
    font-size: 1.5rem;
  }
 }
/* ----------------------------------------------------------------------------------------------------- */

/* EXPERIENCE PAGE */


.experienc-main-container{
  --exp-card-color : #C4B6A5;
  gap: .8rem;
  height: 100%;
}

.experience-hat-pic{
  width: 300px;
  aspect-ratio: 1/1;
}
.experience-column{
  --card-height: 300px;
  --card-width: 330px;
  width: var(--card-width);
  height: var(--card-height);
  background-color: rgba(5, 48, 28, 0.5)  ;
  justify-content: center;
  align-items: center;
  position: relative;
  z-index: 0;
  display: flex;
  transition: all .3s;
  /* box-shadow: 0px 1px 2px 0px rgba(0,255,255,0.7),
              1px 2px 4px 0px rgba(0,255,255,0.7),
              2px 4px 8px 0px rgba(0,255,255,0.7),
              2px 4px 16px 0px rgba(0,255,255,0.7); */
  }
  #column0{ background-color: rgb(88, 24, 151); }
  #column1{ background-color: #E87E00; }
  #column2{ background-color: rgb(0, 128, 43); }
  #column3{ background-color: rgb(128, 0, 100); }
  #column4{ background-color: rgb(0, 13, 128); }
  #column5{ background-color: rgb(0, 128, 122); }

.experience-image{
  height: calc(var(--card-height) - 20px);
  width: calc(var(--card-width) - 20px);
  opacity: 0.9;
}
.experience-ShowMe{
  position: absolute;
  align-self: center;
  z-index:1;
  color: white;
  font-family: var(--nav-font);
  font-size: 1.2rem;
  font-weight: 600;
  background-color: rgba(0, 0, 0, 0.568);
  padding: 2rem;
  text-align: center;
}

.experience-image-text{
  display: flex;
  flex-direction: column;
  padding: 1rem 2rem;
  position: absolute;
  align-self: center;
  z-index:1;
  width: 90%;
  height: 95%;
  display: flex;
  gap: .2rem;
  justify-content: center;
  align-items: center;
  font-family: var(--button-font);
  font-size: 1rem;
  opacity: 0;
  color: black;
}

.experience-skill-container{
  padding-top: .8rem;
  padding-bottom: .8rem;
  display: flex;
  gap: .7rem;
  font-size: .8rem;
}

.experience-position{
  background-color: #FF914D;
  /* background-color: #fd65935e; */
  border-radius: .2rem;
  padding: 1rem;
  /* padding-bottom: .8rem; */
}
#experience-horLine{
  background-color: black;
}
.experience-company, .experience-position{
  font-size: 1.1rem;
  font-weight: 400;
}
.experience-date{
  font-size: 1.3rem;
  padding-bottom: .5rem;
}
.experience-desc{
  padding-bottom: .9rem;
  text-align: center;
}
.experience-button{
  background-color: #9C51A2;
  /* background-color: rgb(255, 90, 153); */
  color: white;
  /* color: black; */
  font-family: var(--nav-button);
  font-weight: 400;
  text-decoration: none;
  border: .2rem solid rgb(73, 17, 0);
  border-radius: .8rem;
  padding: .5rem 4rem;
}
.experience-button:hover{
  background-color: var(--color-yellow);
  color: white;
}
.experience-button:active{
  translate: 0 .5rem;
}

.experience-code-tag{
  color: white;
  font-family: var('--font-button');
  font-size: 1.2rem;
  font-weight: bold;
  background-color: #FF3131;
  /* background-color: var(--color-brik-red); */
  padding: .5rem .8rem;
  border-radius: .5rem;
}
.experience-app-type{
  color: var(--color-red)
}

.experience-column:hover{
  background-color: #FFDE59;
  border: 2px solid var(--color-yellow);
  border-radius: 1rem;
}

.experience-column:hover .experience-ShowMe{
  display: none;
}
.experience-column:hover .experience-image{
  transition: all .3s;
  opacity: .1;
  -moz-transition: opacity 0.8s ease-in-out;
  -webkit-transition: opacity 0.8s ease-in-out;
  -ms-transition: opacity 0.8s ease-in-out;
  transition: opacity 0.8s ease-in-out;
}
.experience-column:hover .experience-image-text{
  transition: all .3s;
  opacity: 1;
}


